import React, {useState} from 'react';
import axios from "axios";
import HeadContainer from "./components/HeadContainer";
import WeatherProperties from "./components/WeatherProperties";
import './App.scss';
import {removeCitiesList} from "./removeList/removeList";
import {Spinner} from "react-bootstrap";


const App = () => {
    const [query, setQuery] = useState('');
    const [weather, setWeather] = useState({});
    const [units, setUnits] = useState('metric');

    const URL = 'https://api.openweathermap.org/data/2.5/weather';
    const API_KEY = '13cf338387990523457c0869f8645ce7';

    const fetchWeather = async (query) => {
        const {data} = await axios.get(URL, {
            params: {
                q: query,
                units: units,
                APPID: API_KEY,
            }
        })
        return data
    }

    const search = async (e) => {
        if (e.target.innerHTML === 'OK' || e.key === 'Enter' || e.target.innerHTML === 'C' || e.target.innerHTML === 'F') {
            const data = await fetchWeather(query);
            setWeather(data);
            removeCitiesList();
        }
    }

    return (
        <div className='wrapper'>
            <div className="main-container">
                <HeadContainer query={query}
                               search={search}
                               weather={weather}
                               setQuery={setQuery}
                               setUnits={setUnits}
                               setWeather={setWeather}
                               units={units}
                />
                {
                    weather.main ?
                        <WeatherProperties weather={weather}/>
                        :
                        <Spinner animation="border" variant="warning" className='spinner'/>
                }
            </div>
        </div>
    )
};

export default App;
